/* Scholarship Form Styles */
.create-scholarship {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.create-scholarship h2 {
  color: #2c3e50;
  font-size: 2rem;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 600;
}

.create-scholarship form {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}

.form-groupss {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-groupss label {
  color: #2c3e50;
  font-weight: 500;
  font-size: 1rem;
}

.form-groupss .form-input,
.form-groupss select,
.form-groupss textarea {
  padding: 0.75rem 1rem;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background-color: #f8fafc;
}

.form-groupss input:focus,
.form-groupss select:focus,
.form-groupss textarea:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
  background-color: #fff;
}

.form-groupss textarea {
  resize: vertical;
  min-height: 100px;
}

.form-groupss input[type="number"] {
  -moz-appearance: textfield;
}

.form-groupss input[type="number"]::-webkit-outer-spin-button,
.form-groupss input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.form-groupss input[type="date"] {
  cursor: pointer;
}

.form-groupss input[type="url"] {
  font-family: monospace;
}

.form-actions {
  grid-column: 1 / -1;
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  margin-top: 1rem;
}

.submit-button {
  background-color: #3b82f6;
  color: #ffffff;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.submit-button:hover {
  background-color: #2563eb;
  transform: translateY(-1px);
}

.cancel-button {
  background-color: #f1f5f9;
  color: #64748b;
  border: 1px solid #e2e8f0;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.cancel-button:hover {
  background-color: #e2e8f0;
  color: #475569;
  transform: translateY(-1px);
}

/* Tag Input Group */
.tag-input-group {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.tag-input-group input {
  flex: 1;
  padding: 0.75rem 1rem;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background-color: #f8fafc;
}

.tag-input-group input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
  background-color: #fff;
}

.add-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 8px;
  background-color: #3b82f6;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.875rem;
  font-weight: 500;
  min-width: 100px;
}

.add-button:hover {
  background-color: #2563eb;
  transform: translateY(-1px);
}

.add-button .icon {
  font-size: 1rem;
}

/* Tag Container */
.tag-container {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 0.5rem;
}

.tag {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 0.75rem;
  background-color: #e0f2fe;
  color: #0369a1;
  border-radius: 6px;
  font-size: 0.875rem;
  font-weight: 500;
}

.remove-tag {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  border: none;
  background: none;
  color: #0369a1;
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 0;
  line-height: 1;
}

.remove-tag:hover {
  color: #0c4a6e;
  transform: scale(1.1);
}

/* Responsive Design */
@media (max-width: 768px) {
  .create-scholarship {
    padding: 1rem;
  }

  .create-scholarship form {
    grid-template-columns: 1fr;
  }

  .form-actions {
    flex-direction: column;
  }

  .submit-button,
  .cancel-button {
    width: 100%;
  }
}

/* Validation Styles */
.form-groupss input:invalid,
.form-groupss select:invalid,
.form-groupss textarea:invalid {
  border-color: #ef4444;
}

.form-groupss input:invalid:focus,
.form-groupss select:invalid:focus,
.form-groupss textarea:invalid:focus {
  box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.1);
}

/* Placeholder Styles */
.form-groupss input::placeholder,
.form-groupss textarea::placeholder {
  color: #94a3b8;
}

/* Required Field Indicator */
.form-groupss label::after {
  content: " *";
  color: #ef4444;
} 